"use client"
import Letter from "./WordGenerator";({data}:{data:string}) => {
  const [word, setWord] = useState(data);
  return (
    <div className="flex items-center justify-center gap-4">
      {data.split("").map((letter: string) => (
        <Letter key={letter} letter={letter} onClick={() => {}} />
      ))}
    </div>
  );
};
export default TextGenerator;